import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import './lib/jquery.magnific-popup.js';
// import './lib/slick.js';
import './lib/owl.carousel.js';
// import './lib/jquery-ui.js';
// import './lib/jquery.nicescroll.js';

$(() => {
	svg4everybody();
});

$(document).ready(function() {

	// Модали
  $('.popup').magnificPopup({
  		type: 'inline',
  		preloader: false,
  		modal: true
  	});

  	$(document).on('click', '.modal-dismiss', function (e) {
  		e.preventDefault();
  		$.magnificPopup.close();
  	});

  	// $.magnificPopup.open({
  	//   items: {
  	//     src: '#modal-call-success'
  	//   },
  	//   type: 'inline',
  	// 	modal: true
  	// });
// Модали конец

	$('.region-select__main').click(function () {
		$('.region-select').toggleClass('is-active');
	});
	$(document).click( function(event){
	if( $(event.target).closest(".region-select").length )
		return;
	$(".region-select").removeClass("is-active");
	event.stopPropagation();
	});


	$('.header-carousel').owlCarousel({
    loop:true,
		items: 1,
		dots:true,
    nav:false
	});

	$('.brands-carousel').owlCarousel({
    loop:true,
		items: 5,
		center:true,
		dots:true,
    nav:true,
		navText: [,],
		responsive: {
			0: {
				items: 1
			},
			520: {
				items: 2
			},
			850: {
				items: 3
			},
			1300: {
				items: 4
			},
			1500: {
				items: 5
			}
		}
	});

	$('.partners-text-carousel').owlCarousel({
    loop:false,
		items: 1,
		dots:true,
    nav:false
	});

	$('.hamburger').click(function () {
		$(this).toggleClass('is-active');
		$('.js-nav').slideToggle('fast');
	})
});



